import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import { getGroups, deleteGroups } from 'services/groupManagement';

class GradedExercisesPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteGroups(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Tên hội',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        width: '25%',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#group/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Quản lý hội">
        <CartHeadAction>
          <BtnAddNew href="#/group/new" />
        </CartHeadAction>
        <Row>
          <Col span={12}>
            <TableData
              reload={this.state.reload}
              service={getGroups}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <GradedExercisesPage />
  </Page>
);



